import kotlinx.css.*
import kotlinx.html.DIV
import kotlinx.html.id
import material.*
import org.w3c.dom.MediaQueryList
import react.*
import react.dom.h1
import react.dom.h2
import styled.StyledDOMBuilder
import styled.css
import styled.styledDiv
import styled.styledHeader
import kotlin.browser.window
import kotlin.reflect.KClass

data class Components(val id: String, val name: String, val content: Any)

val components = listOf(
        Components("buttons", "Buttons", ButtonList::class),
        Components("checkboxes", "Checkboxes", CheckboxList::class),
        Components("fabs", "Floating Action Buttons", FabList::class)
)

external interface AppState: RState {
    var widthLevel: Int
}

class App: RComponent<RProps, AppState>() {
    override fun RBuilder.render() {
        styledHeader {
            css {
                alignContent = Align.center
            }
            attrs.id = "home"
            h1 { +"Material Components" }
        }
        styledDiv {
            css {
                padding(5.px)
            }
            for(component in components) {
                h2 {
                    attrs.id = component.id
                    +component.name }
                child(component.content as KClass<Component<RProps, *>>) {}
            }
        }
        styledDiv {
            css {
                boxSizing = BoxSizing.borderBox
                right = 10.px
                top = 10.px
                position = Position.fixed
            }
            MaterialButtonGroup {
                attrs {
                    variant = "contained"
                    color = "primary"
                    orientation = "vertical"
                }
                for(component in components) {
                    MaterialButton {
                        attrs {
                            href = "#${component.id}"
                        }
                        +component.name
                    }
                }
            }
        }
    }
    override fun AppState.init() {
        fun mediaCheck(widthQuery: String, onTrue: Int, onFalse: Int): MediaQueryList {
            val c = window.matchMedia("only screen and ($widthQuery)")
            c.addListener {
                println("media Check ($widthQuery, ${if(c.matches) onTrue else onFalse})")
                setState {
                    widthLevel = if(c.matches) onTrue else onFalse
                } }
            return c
        }
        if (mediaCheck("max-width: 600px", 1, 2).matches) widthLevel = 1
        if (mediaCheck("min-width: 600px", 2, 1).matches) widthLevel = 2
        if (mediaCheck("min-width: 768px", 3, 2).matches) widthLevel = 3
        if (mediaCheck("min-width: 992px", 4, 3).matches) widthLevel = 4
        if (mediaCheck("min-width: 1200px", 6, 4).matches) widthLevel = 5
    }
}

fun RBuilder.basicContainer(block: StyledDOMBuilder<DIV>.() -> Unit) =
        styledDiv {
            css {
                boxSizing = BoxSizing.borderBox
                display = Display.inlineFlex
                padding(5.px)
            }
            block()
        }

fun nonChild(handler: RBuilder.() -> Unit): ReactElement = buildElement(handler)!!