package material

import basicContainer
import kotlinx.css.Position
import kotlinx.css.position
import kotlinx.html.js.onClickFunction
import react.*
import react.dom.div
import react.dom.p
import styled.css

class FabList: RComponent<RProps, RState>() {
    override fun RBuilder.render() {
        basicContainer {
            MaterialFab {
                attrs { color = "primary" }
                MaterialIcon { +"add" }
            }
        }
        basicContainer {
            MaterialFab {
                attrs {
                    color = "secondary"
                    href = "#home"
                }
                MaterialIcon { +"home" }
            }
        }
        basicContainer {
            MaterialFab {
                attrs { variant = "extended" }
                MaterialIcon { +"navigation" }
                +"Navigate"
            }
        }
        p {}
        basicContainer {
            MaterialFab {
                attrs {
                    size = "medium"
                    color = "primary"
                }
                MaterialIcon { +"add" }
            }
        }
        basicContainer {
            MaterialFab {
                attrs {
                    size = "medium"
                    color = "secondary"
                    href = "#home"
                }
                MaterialIcon { +"home" }
            }
        }
        basicContainer {
            MaterialFab {
                attrs {
                    size = "medium"
                    variant = "extended"
                }
                MaterialIcon { +"navigation" }
                +"Navigate"
            }
        }
        p {}
        basicContainer {
            MaterialFab {
                attrs {
                    size = "small"
                    color = "primary"
                }
                MaterialIcon { +"add" }
            }
        }
        basicContainer {
            MaterialFab {
                attrs {
                    size = "small"
                    color = "secondary"
                    href = "#home"
                }
                MaterialIcon { +"home" }
            }
        }
        basicContainer {
            MaterialFab {
                attrs {
                    size = "small"
                    variant = "extended"
                }
                MaterialIcon { +"navigation" }
                +"Navigate"
            }
        }
        p {}
        child(ZoomingFab::class) {}
    }
}

external interface ZoomingFabState: RState {
    var current: Int
}

data class ZoomFab(val color: String, val icon: String)

class ZoomingFab: RComponent<RProps, ZoomingFabState>() {
    val zoom = listOf(
            ZoomFab("primary", "add"),
            ZoomFab("secondary", "edit"),
            ZoomFab("inherit", "expand"))

    override fun RBuilder.render() {
        div {
            for (i in 0..2) {
                basicContainer {
                    css {
                        position = Position.absolute
                    }
                    attrs.onClickFunction = { setState { current = (current + 1) % zoom.size } }
                    MaterialZoom {
                        attrs {
                            `in` = (i == state.current)
                            unmountOnExit = true
                        }
                        MaterialFab {
                            attrs { color = zoom[i].color }
                            MaterialIcon { +zoom[i].icon }
                        }
                    }
                }
            }
        }
    }

    override fun ZoomingFabState.init() {
        current = 0
    }
}