package material

import basicContainer
import kotlinx.html.id
import nonChild
import react.*
import react.dom.h3
import react.dom.p

class ButtonList: RComponent<RProps, RState>() {
    override fun RBuilder.render() {
        h3 {
            attrs.id = "contained-buttons"
            +"Contained Buttons"
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                }
                +"Default"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                }
                +"Primary"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "secondary"
                }
                +"Secondary"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    disabled = true
                }
                +"Disabled"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                    href = "#contained-buttons"
                }
                +"Link"
            }
        }
        p {}
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                    disableElevation = true
                }
                +"Disable Elevation"
            }
        }
        h3 {
            attrs.id = "text-buttons"
            +"Text Buttons"
        }
        basicContainer {
            MaterialButton { +"Default" }
        }
        basicContainer {
            MaterialButton {
                attrs { color = "primary" }
                +"Primary"
            }
        }
        basicContainer {
            MaterialButton {
                attrs { color = "secondary" }
                +"Secondary"
            }
        }
        basicContainer {
            MaterialButton {
                attrs { disabled = true }
                +"Disabled"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    color = "primary"
                    href = "#text-buttons"
                }
                +"Link"
            }
        }
        h3 {
            attrs.id = "outlined-buttons"
            +"Outlined Buttons"
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                }
                +"Default"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    color = "primary"
                }
                +"Primary"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    color = "secondary"
                }
                +"Secondary"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    disabled = true
                }
                +"Disabled"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    color = "primary"
                    href = "#outlined-buttons"
                }
                +"Link"
            }
        }
        h3 {
            attrs.id = "sizes"
            +"Sizes"
        }
        basicContainer {
            MaterialButton {
                attrs {
                    size = "small"
                }
                +"Small"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    size = "medium"
                }
                +"Medium"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    size = "large"
                }
                +"Large"
            }
        }
        p {}
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    color = "primary"
                    size = "small"
                }
                +"Small"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    color = "primary"
                    size = "medium"
                }
                +"Medium"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    color = "primary"
                    size = "large"
                }
                +"Large"
            }
        }
        p {}
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                    size = "small"
                }
                +"Small"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                    size = "medium"
                }
                +"Medium"
            }
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                    size = "large"
                }
                +"Large"
            }
        }
        h3 {
            attrs.id = "buttons-with-icons-and-label"
            +"Buttons with icons and label"
        }
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "secondary"
                    startIcon = nonChild { MaterialIcon { +"delete" } }
                }
                +"Delete"
            }
        }
        p {}
        basicContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                    endIcon = nonChild { MaterialIcon { +"send" } }
                }
                +"Send"
            }
