                +"Upload"
            }
        }
        h3 {
            attrs.id = "icon-buttons"
            +"Icon buttons"
        }
        basicContainer {
            MaterialIconButton {
                MaterialIcon { +"delete" }
            }
        }
        basicContainer {
            MaterialIconButton {
                attrs {
                    disabled = true
                    color = "primary"
                }
                MaterialIcon { +"delete" }
            }
        }
        basicContainer {
            MaterialIconButton {
                attrs {
                    color = "secondary"
                }
                MaterialIcon { +"alarm" }
            }
        }
        basicContainer {
            MaterialIconButton {
                attrs {
                    color = "primary"
                }
                MaterialIcon { +"add_shopping_cart" }
            }
        }
        h3 {
            attrs.id = "button-group"
            +"Button Group"
        }
        basicContainer {
            MaterialButtonGroup {
                attrs {
                    color = "primary"
                }
                MaterialButton { +"One" }
                MaterialButton { +"Two" }
                MaterialButton { +"Three" }
            }
        }
        basicContainer {
            MaterialButtonGroup {
                attrs {
                    variant = "contained"
                    color = "primary"
                }
                MaterialButton { +"One" }
                MaterialButton { +"Two" }
                MaterialButton { +"Three" }
            }
        }
        basicContainer {
            MaterialButtonGroup {
                attrs {
                    variant = "text"
                    color = "primary"
                }
                MaterialButton { +"One" }
                MaterialButton { +"Two" }
                MaterialButton { +"Three" }
            }
        }
        basicContainer {
            MaterialButtonGroup {
                attrs {
                    color = "primary"
                    orientation = "vertical"
                }
                MaterialButton { +"One" }
                MaterialButton { +"Two" }
                MaterialButton { +"Three" }
            }
        }
        basicContainer {
            MaterialButtonGroup {
                attrs {
                    variant = "contained"
                    color = "primary"
                    orientation = "vertical"
                }
                MaterialButton { +"One" }
                MaterialButton { +"Two" }
                MaterialButton { +"Three" }
            }
        }
        basicContainer {
            MaterialButtonGroup {
                attrs {
                    variant = "text"
                    color = "primary"
                    orientation = "vertical"
                }
                MaterialButton { +"One" }
                MaterialButton { +"Two" }
                MaterialButton { +"Three" }
            }
        }
    }
