package material

import basicContainer
import nonChild
import react.*

class CheckboxList: RComponent<RProps, RState>() {
    override fun RBuilder.render() {
        checkboxContainer {
            onChange = onChangeFunction
        }
        checkboxContainer {
            onChange = onChangeFunction
            icon = nonChild { MaterialIcon { +"favorite_border" } }
            checkedIcon = nonChild { MaterialIcon { +"favorite" } }
        }
        checkboxContainer {
            onChange = onChangeFunction
            label = "Checkbox with label"
        }
        checkboxContainer {
            onChange = onChangeFunction
            icon = nonChild { MaterialIcon { +"favorite_border" } }
            checkedIcon = nonChild { MaterialIcon { +"favorite" } }
            label = "Favorite"
        }
    }
}

val onChangeFunction = { s: CheckboxState -> s.isChecked = !s.isChecked }

fun RBuilder.checkboxContainer(handler: CheckboxProps.() -> Unit): ReactElement {
    return child(CheckboxContainer::class) {
        this.attrs(handler)
    }
}

external interface CheckboxProps: RProps {
    var onChange: (CheckboxState) -> Unit
    var icon: ReactElement?
    var checkedIcon: ReactElement?
    var label: String?
}

external interface CheckboxState: RState {
    var isChecked: Boolean
}

class CheckboxContainer: RComponent<CheckboxProps, CheckboxState>() {
    override fun RBuilder.render() {
        val checkbox = nonChild {
            MaterialCheckbox {
                attrs {
                    checked = state.isChecked
                    onChange = { setState { props.onChange(this) } }
                    props.icon?.let { icon = it }
                    props.checkedIcon?.let { checkedIcon = it }
                }
            }
        }

        basicContainer {
            props.label?.let { _label ->
                MaterialFormControlLabel {
                    attrs {
                        control = checkbox
                        label = _label
                    }
                }
            } ?: child(checkbox)
        }
    }

    override fun CheckboxState.init() {
        isChecked = false
    }
}